//=================================================================== 
// $Panels - Default Panel Styles
//===================================================================
.tile-container {
    margin-top: ce(20px);
}

.panel {
    box-shadow: none;
}

.panel-supplier-items {
    @include border-radius(0px);
    border: 0;
    border-bottom: 1px solid transparent;
    padding: 10px 0;
    background: transparent;
    // min-height: ce(320px);
    .panel-heading {
        @include border-radius(0px);
        border-bottom: 1px solid $gray-light;
        color: $corp-color-dark;
        padding-top: 5px;
        padding-bottom: 5px;
        margin-bottom: 10px;
        position: relative;
    }
    .panel-button {
        @include button($gray-dark);
        @include border-radius(6px);
        padding: 3px 15px;
    }
    .panel-title {
        @include font-size(24px);
        margin-top: ce(5px);
        margin-bottom: ce(5px);
        color: $gray-dark;
    }
    .panel-body {
        padding: 0;
        color: $corp-color-dark;
        h3 {
            color: $corp-color;
        }
    }
    >.table-responsive:last-child>.table:last-child,
    >.table:last-child {
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
    }
    >.supplier-items-list-group .list-group-item,
    >.panel-collapse>.supplier-items-list-group .list-group-item {
        @include border-radius(0);
        // border-bottom: 1px solid $brand-gray-md;
        border-bottom: none;
    }
}

.panel-supplier-items {
    .table {
        th,
        tr,
        td {
            padding-left: 40px;
        }
    }
}

.next-steps-panel {
    min-height: 100%;
    .panel-heading {
        border-bottom: 0;
    }
}

.join-conversation-container {
    .panel-supplier-items {
        min-height: 100%;
    }
}

// =================================================================== 
// $Panels Notifications - Notification Panel styles
// ===================================================================
.Panel-notifications {
    .notPanelHead {
        background-image: none;
        background-color: $corp-color;
        @include border-radius(0);
        &:hover {
            cursor: pointer;
        }
    }
    .notPanelGroup {
        margin-bottom: 10px;
    }
    .notPanelBody {
        @include border-radius(0);
        font-size: 16px;
        color: $corp-color-dark;
    }
    .supplier-items_notificationBigTitle {
        color: $gray-light;
        font-size: 18px;
        font-weight: normal;
        font-family: 'Segoe UI Light';
    }
}

// Sidebar panels
.sidebar-dialog-pane {
    background-color: $white;
    min-width: ce(150px);
    max-width: ce(400px);
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    z-index: 300;
    overflow-y: auto;
}

.sd-pane-clicked {
    @include transition(all 0.5s ease-in-out);
    height: calc(100% - 72px);
}

.sd-pane-hidden {
    @include transition(all 0.5s ease-in-out);
    height: calc(100% - 72px);
    left: -500px;
}

.sidebar-dialog {
    min-width: ce(150px);
    max-width: ce(400px);    
    background-color: $white;
    hr{
        border: 1px solid  $surfmist-mid;
    }
}

.sd-header {
    @include font-size(18px);
    color: $monument-dark;
    border-bottom: 2px solid $surfmist-mid;
    font-weight: normal;
    margin-bottom: 20px;
    padding: 15px;
}

.sd-content {
    max-width: ce(400px);
    overflow: auto;
    height: 100%;
    padding: 10px 15px;
}

.sd-button-container {
    margin-top: 10px;
    margin-bottom: 15px;
    padding: 10px 15px;
}
